@import url(https://fonts.googleapis.com/css?family=Milonga);
body {
  margin: 0;
  padding: 0;
  background-color: #282c34;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button:disabled {
  cursor: not-allowed !important;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}


:root {
	--main-border-color: rgb(146, 255, 112);
	--main-font-size: 15px; 
}

.App {
    text-align: center;
	padding-bottom: 1vw;
	font-family: 'Milonga', cursive;
}

.photo-editor {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
}

.title {
	font-family: 'Milonga', cursive;
	color: rgb(146, 255, 112);
	color: var(--main-border-color);
	width: 100%;
	margin: 1vw auto 2.25vw auto;
	background: -webkit-linear-gradient(#eee, #ccc, var(--main-border-color));
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

@media screen and (max-width: 1025px) {
	.photo-editor {
		-webkit-flex-direction: column;
		        flex-direction: column;
	}
}

@media screen and (max-width: 650px) {
	.title {
		margin: 3vw auto 5.5vw auto;

		font-size: 27px;
	}
}


.image-input {
    background-color: rgb(135, 222, 135);
    border-radius: 10px;
    border: 2px solid var(--main-border-color);
    margin-top: 0.7vw;
    padding: 0;
  
    font-size: var(--main-font-size);
}

.image-container {
    margin: 7vw auto 0 auto;
}
  
.uploaded-image {
    display: block;
    margin: 0 auto;
    width: 25vw;
}

@media screen and (max-width: 1025px) and (min-width: 650px) {
    .image-container {
        margin: 10vw auto 5vw auto;
    }
}

@media screen and (max-width: 650px) {
    .uploaded-image {
        width: 40vw;
    }

    .image-container {
        margin: 13vw auto 9vw auto;
    }
}
.main-button {
	padding: 7px 14px;
	margin: 10px;

    border: 2px solid var(--main-border-color);
    background-color: rgb(135, 222, 135);
    border-radius: 20px;

    font-size: var(--main-font-size);
	font-weight: bold;
	font-family: 'Milonga', cursive;
}

.main-button:hover {
	cursor: pointer;
}

.black-button {
    border: 2px solid rgb(206, 206, 206);
    background-color: black;
    color: #eee;
}

.white-button {
    border: 2px solid black;
    background-color: #eee;
    color: black;
}

.reset-button {
    border: 2px solid rgb(156, 39, 39);
    background-color: red;
    color: #fff;
}

@media screen and (max-width: 1025px) {
	.main-button {
		margin: 15px;
	}
}

@media screen and (max-width: 650px) {
	.main-button {
		margin: 10px;

		font-size: 13px;
	}
}


.rotation-input {
    width: 7.75vw;
	text-align: center;
}

.rotation-input:hover {
    cursor: default;
}

.filter-panel {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 1vw auto 2vw auto;
    width: 25vw;
    color: white;
}

.filter-panel__label {
    margin-top: 1.25vw;
    margin-bottom: 0.5vw;

    font-size: 17px;
}

input[type=range] {
    -webkit-appearance: none;
}

input[type=range]::-webkit-slider-runnable-track {
    width: 300px;
    height: 8px;
    background: linear-gradient(90deg, #666, #eee);
}

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 2px solid var(--main-border-color);
    border-radius: 50%;
    height: 17px;
    width: 17px;
    background: rgb(95, 168, 95);
    margin-top: -5px;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: linear-gradient(90deg, #666, #eee);
}

@media screen and (max-width: 1025px) and (min-width: 650px) {

    .options-section {
        margin-top: 5vw;
    }
	
	.rotation-input {
		width: 15vw;
	}

	.filter-panel {
		margin: 3vw auto;
		width: 60vw;
	}

	.filter-panel__label {
		margin-top: 2vw;
		margin-bottom: 1.25vw;
	}
}

@media screen and (max-width: 650px) {

    .options-section {
        margin-top: 4vw;
    }

	.rotation-input {
        width: 25vw;
        margin: 0 20vw 10px 20vw;
	}

	.filter-panel {
		margin: 3vw auto;
		width: 60vw;
	}

	.filter-panel__label {
		margin-top: 2.5vw;
        margin-bottom: 2vw;
        
        font-size: 14px;
	}
}
